.toggle {
  cursor: pointer;
  display: inline-block;
  position: absolute;
  width: 90px;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  height: 40px;
  background-color: #dcc8a3;
  border-radius: 84px;
  margin-left: 45px;
  z-index: 2;
  -webkit-transition: background-color 200ms cubic-bezier(0.445, 0.05, 0.55, 0.95);
  transition: background-color 200ms cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

.toggle__handler {
  display: inline-block;
  position: relative;
  z-index: 1;
  top: 3px;
  left: 3px;
  width: 34px;
  height: 34px;
  background-color: #7e2804;
  border-radius: 50px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
  -webkit-transition: all 400ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
  transition: all 400ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

input:checked + .toggle:after {
  color: #ffffff;
}
input:checked + .toggle .toggle__handler {
  -webkit-transform: translate3d(49px, 0, 0) rotate(0);
          transform: translate3d(49px, 0, 0) rotate(0);
}
input:checked + .toggle + .bear-body {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.bear-body {
  position: relative;
  border-radius: 20px 10px 5px;
  border-bottom: 105px solid #7e2804;
  border-left: 20px solid transparent;
  border-right: 10px solid transparent;
  height: 0;
  width: 88px;
}
.bear-body::before, .bear-body::after {
  content: "";
  position: absolute;
  height: 25px;
  width: 25px;
  border-radius: 50%;
  top: -16px;
  background: -webkit-radial-gradient(#dcc8a3 5px, #7e2804 6px);
  background: radial-gradient(#dcc8a3 5px, #7e2804 6px);
}
.bear-body::before {
  left: -12.5px;
}
.bear-body::after {
  right: -12.5px;
}
.bear-body .eye {
  position: absolute;
  top: 18px;
  background: #3d2526;
  height: 14px;
  width: 14px;
  border-radius: 50%;
  background-image: -webkit-radial-gradient(4px, circle, white 1px, #3d2526 2px);
  background-image: radial-gradient(circle at 4px, white 1px, #3d2526 2px);
}
.bear-body .eye.left {
  left: 20px;
  -webkit-animation: blink 3s ease-in-out infinite;
          animation: blink 3s ease-in-out infinite;
}
.bear-body .eye.right {
  left: 46px;
  -webkit-animation: blink 3.5s ease-in-out infinite;
          animation: blink 3.5s ease-in-out infinite;
}

@-webkit-keyframes blink {
  85% {
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
  }
  90% {
    -webkit-transform: rotateX(90deg);
            transform: rotateX(90deg);
  }
}

@keyframes blink {
  85% {
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
  }
  90% {
    -webkit-transform: rotateX(90deg);
            transform: rotateX(90deg);
  }
}
